h1, h2, h3, h4 {
  font-family: $font-family;
  font-weight: $bold;
  line-height: $line-height/1.3;
  -webkit-font-smoothing: antialiased; }

h1 {
  font-size: responsive 1.3rem 2.5rem; }

h2 {
  margin-bottom: .7rem;
  font-size: responsive 2rem 3.5rem;
  @media all and (min-width: $bp-med) {
    margin-bottom: 2rem; } }
