@lost gutter 30px {}

::selection {
  background-color: darken($bgc, 4%); }

::-moz-selection {
  background-color: darken($bgc, 4%); }

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: aller, sans-serif;
  background-color: $bgc;
  text-rendering: optimizeLegibility; }

.global-nav {
  z-index: 500;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  padding: 1rem;
  @media all and (min-width: $bp-med) {
    padding: 3rem 3rem 0 3rem; } }

.global-content {
  lost-center: 100%;
  position: relative;
  overflow: hidden;
  height: 100%;
  img {
    user-select: none; } }

.section-title {
  z-index: -100;
  position: absolute;
  right: -1rem;
  top: -3rem;
  display: block;
  width: 100vw;
  font-size: responsive 8rem 30rem;
  word-wrap: break-word;
  text-transform: uppercase;
  text-align: right;
  font-weight: $bold;
  line-height: .8;
  color: $darkbgc;
  user-select: none;
  @media all and (min-width: $bp-med) {
    right: -3rem;
    top: -6rem; } }

#skew-bg {
  z-index: -100;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%; }

#home {
  #skew-bg {
    left: -50px;
    @media all and (min-width: $bp-med) {
      left: -500px; } } }

#catalog, #about, #blog {
  #skew-bg {
    left: -5px;
    @media all and (min-width: $bp-med) {
      left: -200px; } } }

.container {
  lost-center: 1440px flex;
  padding: 1rem 2rem;
  @media all and (min-width: $bp-med) {
    lost-center: 1440px;
    padding: 5rem 3rem; } }

ul,ol {
  list-style: none; }

strong, b {
  font-weight: 700; }

i, em {
  font-style: italic; }

.global-footer {
  lost-center: 100%;
  margin-top: 8rem;
  padding: 2rem;
  color: $bgc;
  background: rgba($darkblue, .95);
  @media all and (min-width: $bp-med) {
    margin-top: 15rem;
    padding: 2rem  0 1rem; }
  a {
    color: $bgc;
    border-color: $bgc; }
  .footer {
    lost-center: 960px;
    font-size: responsive .8rem 1rem;
    font-weight: $light;
    text-align: center;
    @media all and (min-width: $bp-med) {
      text-align: inherit; }
    .newsletter {
      @media all and (min-width: $bp-med) {
        text-align: right; } }
    .newsletter-input {
      padding: 14px 16px 14px 12px;
      border-radius: 4px;
      border: 1px solid $bgc;
      color: $darkblue;
      background: $bgc; }
    .newsletter-button {
      display: inline-block;
      margin-top: 1rem;
      padding: 14px 16px 12px 12px;
      border-radius: 4px;
      border: 1px solid $bgc;
      background: transparent;
      &:active,
      &:hover {
        background: $bgc;
        color: $darkblue;
        transform: translateY(1px);
        box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);
        transition: all .25s ease; } }
    h3 {
      margin: 0;
      font-size: responsive .8rem 1rem;
      font-weight: $bold; } }
  .contact {
    @media all and (min-width: $bp-med) {
      lost-column: 6/12; }
    .social {
      margin-top: 1rem;
      li {
        display: inline-block;
        margin-right: .5rem; }
      a {
        border: none;
        svg {
          height: 28px;
          fill: $bgc; } } }
    .info-foot {
      li {
        margin-top: .5rem; } } }
  .newsletter {
    @media all and (min-width: $bp-med) {
      lost-column: 6/12; } }
  .copy {
    lost-center: 960px;
    margin-top: 1rem;
    padding: 1rem 0;
    text-align: center;
    border-top: 1px solid darken($darkblue, 1%);
    p, a {
      color: rgba($bgc, .8);
      font-size: responsive .75rem .9rem; }
    ul {
      display: flex;
      margin: 0;
      justify-content: center;
      li {
        margin-right: 1rem;
        &:last-child {
          margin: 0; } } } } }
