.nosotros {
  lost-center: 1440px;
  padding: 0 3rem;
  margin-top: 8rem;
  @media all and (min-width: $bp-med) {
    margin-top: 15rem; }
  // lost-utility: edit
  .nosotros-block-left, .nosotros-block-right {
    lost-utility: clearfix;
    h1 {
      color: $blue; }
    .left, .right {
      position: relative; } }

  .nosotros-block-left {
    .left {
      margin-bottom: 2rem;
      @media all and (min-width: $bp-med) {
        lost-column: 4/12;
        margin-bottom: 0; } }
    .right {
      @media all and (min-width: $bp-med) {
        lost-column: 8/12; }
      .img-bot {
        position: absolute;
        right: 0;
        width: 70%;
        height: 35vh;
        opacity: .85;
        @media all and (min-width: $bp-med) {
          right: 8rem;
          width: 80%;
          height: 60vh; }
        img {
          height: 35vh;
          object-fit: cover;
          @include object-fit(cover);
          @media all and (min-width: $bp-med) {
            height: 60vh; } } }
      .img-top {
        z-index: 100;
        position: absolute;
        top: 5rem;
        left: 0;
        width: 60%;
        height: 40vh;
        opacity: .9;
        @media all and (min-width: $bp-med) {
          width: 50%;
          height: 60vh;
          top: 15rem;
          right: 0; }
        img {
          height: 40vh;
          box-shadow: 0 7px 42px rgba($black, .2);
          object-fit: cover;
          object-position: 75% 50%;
          @include object-fit(cover, 75% 50%);
          @media all and (min-width: $bp-med) {
            height: 60vh; } } } } }

  .nosotros-block-right {
    display: flex;
    flex-flow: row wrap;
    margin-top: 21rem;
    @media all and (min-width: $bp-med) {
      flex-flow: inherit;
      margin-top: 13rem; }
    .left {
      order: 2;
      display: block;
      width: 100%;
      @media all and (min-width: $bp-med) {
        lost-column: 8/12;
        order: inherit; }
      .img-bot {
        z-index: 100;
        position: absolute;
        top: 3rem;
        right: 0;
        width: 80%;
        height: 40vh;
        opacity: .85;
        @media all and (min-width: $bp-med) {
          top: 10rem;
          left: 6rem;
          height: 50vh; }
        img {
          height: 40vh;
          box-shadow: 0 7px 42px rgba($black, .2);
          object-fit: cover;
          @include object-fit(cover);
          @media all and (min-width: $bp-med) {
            height: 50vh; } } }
      .img-top {
        margin-top: 15rem;
        width: 70%;
        height: 40vh;
        opacity: .9;
        @media all and (min-width: $bp-med) {
          margin-top: 25rem;
          width: 50%;
          height: 60vh; }
        img {
          height: 40vh;
          object-fit: cover;
          object-position: 75% 50%;
          @include object-fit(cover, 75% 50%);
          @media all and (min-width: $bp-med) {
            height: 60vh; } } } }
    .right {
      order: 1;
      display: block;
      width: 100%;
      @media all and (min-width: $bp-med) {
        lost-column: 4/12;
        order: inherit;
        padding-top: 10rem; } } } }
